#adtoniq-msg-bar {
  position: fixed;
  bottom: -20%;
  left: 0;
  width: 100%;
  min-height: 30px;
  z-index: 99999;
  padding: 15px 0;
  font-family: sans-serif;
  font-size: 16px;
  text-align: center;
  background: #222;
  color: #fff;
  transition: bottom 500ms ease-in-out;
  p {
    margin: 0 0 10px !important;
  }
  a {
    color: #fff !important;
    text-decoration: underline !important;
    &:hover {
      color: #d3d3d3 !important;
      text-decoration: underline !important;
    }
  }
  &.reveal-alert {
    bottom: 0;
    transition: bottom 500ms ease-in-out;
  }
  .btn-msg-bar {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    border: 1px solid transparent;
    white-space: nowrap;
    padding: 10px 30px;
    border-radius: $border-radius;
    background-color: $brand-primary;
    color: #fff !important;
    user-select: none;
    &:hover {
      background: darken($brand-primary, 10%);
    }
    + .btn-msg-bar {
      margin-left: 10px;
    }
  }
  #adtoniq-msg {
    display: inline;
  }
  #adtoniq-btns {
    display: inline;
  }
}
